document.addEventListener('DOMContentLoaded', function(){

    if(document.location.pathname.startsWith('/admin')){
        if(document.location.pathname.endsWith('/area-admin') || document.location.pathname.endsWith('/stadisticas')){
            graficos();
        }
    }
    eventListeners();

});

